import {
  Card,
  CardActionArea,
  CardContent,
  Grid,
  styled,
  Typography,
} from "@mui/material";
import React from "react";

const GridItem = styled(Grid)(({ theme }) => ({
  padding: "10px 0",
  width: "100%",
}));

const AppCards = (props) => {
  const item = props.info;

  const ttt = () => {
    alert("Run: " + item.title);
  };

  return (
    <GridItem item xs={12} sm={6} md={3}>
      <Card sx={{ maxWidth: 345 }} title={item.description}>
        <CardActionArea>
          <CardContent sx={{ flexGrow: 1 }}>
            <Typography gutterBottom variant="h5">
              {item.title}
            </Typography>
          </CardContent>
        </CardActionArea>
      </Card>
    </GridItem>
  );
};

export default AppCards;
