import React from "react";

import { Container } from "@mui/material";
import { getMenuCards } from "../data/utils";
import AppCardsAccordion from "../components/Home/app-cards-accordion";

function BmvHome(props) {
  const menuCards = getMenuCards();

  return (
    <Container maxWidth="80%" sx={{ marginTop: "10px" }}>
      {menuCards.map((item, idx) => {
        return (
          <AppCardsAccordion
            key={idx}
            title={item.menuHeader}
            subMenu={item.subMenu}
          />
        );
      })}
    </Container>
  );
}

export default BmvHome;
