import React, { useState } from "react";
import { BrowserRouter, Routes, Route } from "react-router-dom";

import { Box, createTheme, Link, Stack, ThemeProvider } from "@mui/material";
import "./App.css";
import Navbar from "./components/Menu/Navbar";
import BmvHome from "./pages/bmv-home";
import PageOne from "./pages/hdrOne/page-one";
import PageTwo from "./pages/hdrTwo/page-two";
import PageThree from "./pages/hdrThree/page-three";
import PageFour from "./pages/hdrFour/page-four";
import PageFive from "./pages/hdrFive/page-five";
import PageSix from "./pages/hdrSix/page-six";
import PageSeven from "./pages/hdrSeven/page-seven";

function App() {
  const [mode, setMode] = useState("light");

  const darkTheme = createTheme({
    palette: {
      mode: mode,
    },
  });

  return (
    <BrowserRouter>
      <ThemeProvider theme={darkTheme}>
        <Box bgcolor={"background.default"} color={"text.primary"}>
          <Navbar />
          <Routes>
            <Route path="/" element={<BmvHome />} />
            <Route path="/page1" element={<PageOne />} />
            <Route path="/page2" element={<PageTwo />} />
            <Route path="/page3" element={<PageThree />} />
            <Route path="/page4" element={<PageFour />} />
            <Route path="/page5" element={<PageFive />} />
            <Route path="/page6" element={<PageSix />} />
            <Route path="/page7" element={<PageSeven />} />
          </Routes>
        </Box>
        <Stack spacing={2} direction="row" marginTop={2} marginLeft={2}>
          <Link href="/">Home</Link>
          <Link href="/page1">Page-1</Link>
          <Link href="/page2">Page-2</Link>
          <Link href="/page3">Page-3</Link>
          <Link href="/page4">Page-4</Link>
          <Link href="/page5">Page-5</Link>
          <Link href="/page6">Page-6</Link>
          <Link href="/page7">Page-7</Link>
        </Stack>
      </ThemeProvider>
    </BrowserRouter>
  );

}

export default App;
