import {
  AppBar,
  Container,
  Toolbar,
} from "@mui/material";
import DriveEtaIcon from "@mui/icons-material/DriveEta";
import MdMenu from "./md-menu";
import XsMenu from "./xs-menu";
import MenuLogin from "./menu-login";

const Navbar = (props) => {

  return (
    <AppBar position="sticky">
      <Container maxWidth="xl">
        <Toolbar disableGutters>
          <DriveEtaIcon
            sx={{ display: { xs: "none", md: "flex" }, mr: 1 }}
            fontSize="large"
          />
          <MdMenu />
          <XsMenu />
          <DriveEtaIcon sx={{ display: { xs: "flex", md: "none" }, mr: 1 }} />
          <MenuLogin />
        </Toolbar>
      </Container>
    </AppBar>
  );
};

export default Navbar;
