import MenuData from './menu.json';

export const getHeaderMenu = () => {
  const menuHeader = MenuData.items.map((item, idx) => {
    return item.title;
  });
  return menuHeader;
};

export const getSubHeaderMenu = (menuItem) => {
  const menuSubHeader = MenuData.items.filter((item) => item.title === menuItem);
  return menuSubHeader[0].subMenu;
};

export const getAllMenu = () => {
  const allMenu = [];
  MenuData.items.forEach(element => {
    element.subMenu.forEach(subElement => {
      allMenu.push(subElement);
    })
  })

  return allMenu;
}

export const getMenuCards = () => {
  const menuHeader = MenuData.items.map((item, idx) => {
    return item;
  });
  return menuHeader;
};
