import { Box } from "@mui/system";
import React from "react";
import { getHeaderMenu } from "../../data/utils";
import BtnMenu from "./btn-menu";

function MdMenu(props) {
  const menuList = getHeaderMenu();

  return (
    <Box gap={1} sx={{ flexGrow: 1, display: { xs: "none", md: "flex" } }}>
      {menuList.length > 0 && menuList.map((item, idx) => {
        return (<BtnMenu key={idx} title={item} />)
      })}
    </Box>
  );
}

export default MdMenu;
