import React from "react";

import { Button, Menu, MenuItem } from "@mui/material";
import { getSubHeaderMenu } from "../../data/utils";
import KeyboardArrowDownIcon from '@mui/icons-material/KeyboardArrowDown';

function BtnMenu(props) {
  const [anchorEl, setAnchorEl] = React.useState(null);
  const open = Boolean(anchorEl);

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const subMenu = getSubHeaderMenu(props.title);

  return (
    <>
      <Button
        color="inherit"
        id="basic-button"
        aria-controls={open ? "basic-menu" : undefined}
        aria-haspopup="true"
        aria-expanded={open ? "true" : undefined}
        onClick={handleClick}
        endIcon={<KeyboardArrowDownIcon />}
      >
        {props.title}
      </Button>
      <Menu
        id="basic-menu"
        anchorEl={anchorEl}
        open={open}
        onClose={handleClose}
        MenuListProps={{
          "aria-labelledby": "basic-button",
        }}
      >
        {subMenu.length > 0 && subMenu.map((item, idx) => {
          let menuItem = <MenuItem disabled key={idx} onClick={handleClose}>{item.title}</MenuItem>;
          if (item.active) {

            menuItem = <MenuItem key={idx} onClick={handleClose}>{item.title}</MenuItem>;
          }
          return menuItem;
        })}
      </Menu>
    </>
  );
}

export default BtnMenu;
